import React from 'react';
import PopupWithForm from './PopupWithForm';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function EditProfilePopup({ isOpen, onClose, onUpdateUser }) {
  const currentUser = React.useContext(CurrentUserContext);
  const [data, setData] = React.useState({
    name: '',
    age: '',
    city: '',
    education: '',
  });

  function handleChange(e) {
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value,
    });
  }

  React.useEffect(() => {
    setData({
      name: currentUser.name,
      age: currentUser.age,
      city: currentUser.city,
      education: currentUser.education,
    });
    // setName(currentUser.name);
    // setAge(currentUser.age);
  }, [currentUser, isOpen]);

  function handleSubmit(e) {
    e.preventDefault();
    const { name, age, city, education } = data;

    onUpdateUser({
      name,
      age,
      city,
      education,
    });
  }

  return (
    <PopupWithForm
      name="popup__form_submit"
      title="Редактировать профиль"
      buttonTitle="Сохранить"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      buttonText={'Сохранить'}
    >
      <input
        type="text"
        placeholder="Введите имя"
        className="popup__input popup__input_type_name"
        name="name"
        value={data.name}
        onChange={handleChange}
        required
        minLength="3"
        maxLength="30"
      />

      <input
        type="number"
        placeholder="Возвраст"
        className="popup__input popup__input_type_age"
        name="age"
        value={data.age}
        onChange={handleChange}
        required
        min="10"
        max="99"
      />

      <input
        type="text"
        placeholder="Город проживания"
        className="popup__input popup__input_type_education"
        name="city"
        value={data.city}
        onChange={handleChange}
        required
        minLength="3"
        maxLength="30"
      />

      <input
        type="text"
        placeholder="ВУЗ в котором учились"
        className="popup__input popup__input_type_education"
        name="education"
        id="job-input"
        value={data.education}
        onChange={handleChange}
        required
        minLength="3"
        maxLength="30"
      />
    </PopupWithForm>
  );
}

export default EditProfilePopup;
